body {
    margin: 0;
    padding: 0;
}

nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 50px;
    width: 100%;
    background: rgb(58, 52, 52);
    color: white;
    position: fixed;
    top: 0;
    z-index: 1;
}

.nav-links {
    width: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
}

.nav-link {
    color: white;
}

h1 {
    margin-top: 100px;
    text-align: center;
}

.home-img {
    width: 40%;
    display: block;
    margin: 0 auto;
}

.item-img {
    width: 95%;
}

.items-holder {
    display: flex;
}